import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import classnames from "classnames/bind";
import style from "./PostEditor.module.scss";
import markdownToHtml from "../lib/markdownToHtml";
import { CATEGORY } from "@/lib/constants";

const cx = classnames.bind(style);

interface PostEditorProps {
  initialTitle: string;
  initialContent: string;
  initialCategory: string;
  initialDescription?: string;
  initialCoverImage?: string;
  initialTags?: string;
  categoryList: string[];
  onSubmit: (postData: {
    title: string;
    content: string;
    category: string;
    description: string;
    coverImage: string;
    tags: string;
  }) => Promise<void>;
  submitButtonText: string;
  isSubmitting: boolean;
}

export default function PostEditor({
  initialTitle = "",
  initialContent = "",
  initialCategory = "",
  initialDescription = "",
  initialCoverImage = "",
  initialTags = "",
  categoryList = [],
  onSubmit,
  submitButtonText,
  isSubmitting,
}: PostEditorProps) {
  const [title, setTitle] = useState(initialTitle);
  const [content, setContent] = useState(initialContent);
  const [category, setCategory] = useState(initialCategory || categoryList[0] || "");
  const [description, setDescription] = useState(initialDescription);
  const [coverImage, setCoverImage] = useState(initialCoverImage);
  const [tags, setTags] = useState(initialTags);
  const [error, setError] = useState("");
  const [isUploading, setIsUploading] = useState(false);
  const [htmlContent, setHtmlContent] = useState("");
  const [cursorLine, setCursorLine] = useState(0);
  const [isGeneratingIndex, setIsGeneratingIndex] = useState(false);
  const [selectedLineText, setSelectedLineText] = useState("");
  const [showContextMenu, setShowContextMenu] = useState(false);
  const [menuPosition, setMenuPosition] = useState({ x: 0, y: 0 });

  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const previewRef = useRef<HTMLDivElement>(null);
  const editorContainerRef = useRef<HTMLDivElement>(null);
  const previewContainerRef = useRef<HTMLDivElement>(null);
  const isScrollingSyncRef = useRef(false);
  const contextMenuRef = useRef<HTMLDivElement>(null);

  // ÎßàÌÅ¨Îã§Ïö¥ÏùÑ HTMLÎ°ú Î≥ÄÌôò
  useEffect(() => {
    const convertMarkdown = async () => {
      try {
        const html = await markdownToHtml(content);
        setHtmlContent(html);
      } catch (error) {
        console.error("ÎßàÌÅ¨Îã§Ïö¥ Î≥ÄÌôò Ïò§Î•ò:", error);
      }
    };

    convertMarkdown();
  }, [content]);

  // Ìé∏ÏßëÍ∏∞ Ïª§ÏÑú ÏúÑÏπòÎ°ú ÌîÑÎ¶¨Î∑∞ Ïä§ÌÅ¨Î°§ Ïù¥Îèô
  const scrollToText = (text: string) => {
    const container = previewContainerRef.current;

    if (container) {
      // container ÏïàÏùò Î™®Îì† p ÏöîÏÜåÎ•º Í∞ÄÏ†∏Ïò¥
      const paragraphs = container.querySelectorAll("*");
      if (paragraphs.length > 0) {
        // Í∞Å p ÏöîÏÜåÏóêÏÑú ÌÖçÏä§Ìä∏Î•º Ï∞æÏïÑ Ìï¥Îãπ ÏúÑÏπòÎ°ú Ïä§ÌÅ¨Î°§ Ïù¥Îèô
        for (let i = 0; i < paragraphs.length; i++) {
          console.log(paragraphs[i].textContent);
          if (paragraphs[i].textContent?.includes(text)) {
            // console.log(i, paragraphs[i]);
            // Ï∞æÏùÄ ÌÖçÏä§Ìä∏Î•º Í∞ÄÏßÑ ÏöîÏÜåÎ°ú Ïä§ÌÅ¨Î°§
            // paragraphs[i].scrollIntoView({ behavior: "smooth", block: "start" });
            // Ìï¥Îãπ ÏöîÏÜåÏùò ÏúÑÏπòÎ•º Í≥ÑÏÇ∞
            const rect = paragraphs[i].getBoundingClientRect();
            const containerRect = container.getBoundingClientRect();

            // ÏöîÏÜåÏùò Ïä§ÌÅ¨Î°§ ÏúÑÏπò Í≥ÑÏÇ∞
            const scrollTop = rect.top - containerRect.top + container.scrollTop - 200;

            // Ïä§ÌÅ¨Î°§ ÏúÑÏπòÎ°ú Ïù¥Îèô
            container.scrollTo({ top: scrollTop, behavior: "smooth" });

            break;
          }
        }
      }
    }
  };

  // Ìé∏ÏßëÍ∏∞ ÌÅ¥Î¶≠Ïãú ÌòÑÏû¨ ÎùºÏù∏ Í∞ÄÏ†∏Ïò§Îäî Ìï®Ïàò
  const handleCursorPosition = (e: any) => {
    const textarea = e.target;
    const cursorPosition = textarea.selectionStart;

    // textarea Ï†ÑÏ≤¥ ÌÖçÏä§Ìä∏
    const textValue = textarea.value;

    if (textValue.trim() === "") return;

    // Ïª§ÏÑú Ïù¥Ï†Ñ ÌÖçÏä§Ìä∏ Ï§ëÏóêÏÑú Í∞ÄÏû• Í∞ÄÍπåÏö¥ Ï§ÑÎ∞îÍøà (\n)ÏùÑ Ï∞æÏùå
    const startOfLine = textValue.lastIndexOf("\n", cursorPosition - 1) + 1;

    // Ïª§ÏÑú Ïù¥ÌõÑ ÌÖçÏä§Ìä∏ Ï§ëÏóêÏÑú Í∞ÄÏû• Í∞ÄÍπåÏö¥ Ï§ÑÎ∞îÍøà (\n)ÏùÑ Ï∞æÏùå
    const endOfLine = textValue.indexOf("\n", cursorPosition);

    // Ï§Ñ Ï†ÑÏ≤¥Î•º Ï∂îÏ∂ú (Ïª§ÏÑúÍ∞Ä ÏûàÎäî Ï§ÑÏùò ÌÖçÏä§Ìä∏)
    const currentLineText = textValue.substring(startOfLine, endOfLine === -1 ? textValue.length : endOfLine);
    console.log(currentLineText);
    scrollToText(removeHyphens(currentLineText));
    // setCurrentLine(currentLineText);
  };

  // ÎçîÎ∏î ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨
  const handleDoubleClick = (e: React.MouseEvent<HTMLTextAreaElement>) => {
    const textarea = e.currentTarget;
    const cursorPosition = textarea.selectionStart;
    const textValue = textarea.value;

    // ÌòÑÏû¨ ÎùºÏù∏Ïùò ÏãúÏûëÍ≥º ÎÅù ÏúÑÏπò Ï∞æÍ∏∞
    const startOfLine = textValue.lastIndexOf("\n", cursorPosition - 1) + 1;
    let endOfLine = textValue.indexOf("\n", cursorPosition);
    if (endOfLine === -1) endOfLine = textValue.length;

    // ÎùºÏù∏ Ï†ÑÏ≤¥ ÏÑ†ÌÉù
    textarea.setSelectionRange(startOfLine, endOfLine);

    // ÏÑ†ÌÉùÎêú ÌÖçÏä§Ìä∏ Ï†ÄÏû•
    const selectedText = textValue.substring(startOfLine, endOfLine);
    setSelectedLineText(selectedText);

    // ÎßàÏö∞Ïä§ Ïù¥Î≤§Ìä∏Ïùò ÏúÑÏπò Ï†ïÎ≥¥Î•º ÏÇ¨Ïö©ÌïòÏó¨ Î©îÎâ¥ ÏúÑÏπò Í≥ÑÏÇ∞
    // Ïù¥ Î∞©ÏãùÏùÄ textareaÏùò Ïä§ÌÅ¨Î°§ ÏúÑÏπòÏóê Í¥ÄÍ≥ÑÏóÜÏù¥ Ï†ïÌôïÌïú ÏúÑÏπòÎ•º Ï†úÍ≥µ
    const menuWidth = 140; // Ïª®ÌÖçÏä§Ìä∏ Î©îÎâ¥ ÎÑàÎπÑ

    // ÌôîÎ©¥ Í≤ΩÍ≥Ñ Í≥†Î†§ÌïòÏó¨ Î©îÎâ¥ ÏúÑÏπò Ï°∞Ï†ï
    const x = Math.min(e.clientX, window.innerWidth - menuWidth - 20);

    // ÌÅ¥Î¶≠ ÏúÑÏπò ÏïÑÎûòÏóê Î©îÎâ¥ ÌëúÏãú (ÎùºÏù∏ ÎÜíÏù¥Ïùò Ï†àÎ∞ò Ï†ïÎèÑ ÏïÑÎûò)
    const lineHeight = parseInt(getComputedStyle(textarea).lineHeight) || 20;
    const y = e.clientY + lineHeight / 2;

    setMenuPosition({ x, y });
    setShowContextMenu(true);
  };

  // Ïª®ÌÖçÏä§Ìä∏ Î©îÎâ¥ Ïô∏Î∂Ä ÌÅ¥Î¶≠ Ïãú Îã´Í∏∞
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (contextMenuRef.current && !contextMenuRef.current.contains(event.target as Node)) {
        setShowContextMenu(false);
      }
    };

    if (showContextMenu) {
      document.addEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showContextMenu]);

  // Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ± Ìï∏Îì§Îü¨
  const handleGenerateImage = async () => {
    try {
      setError("");
      setIsUploading(true);

      const response = await fetch("http://localhost:3001/api/gpt/generate-image", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          title: selectedLineText,
        }),
      });

      if (!response.ok) {
        throw new Error("Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }

      const result = await response.json();

      if (result.success && result.data.localPath) {
        // Ïù¥ÎØ∏ÏßÄ URLÏùÑ ÏóêÎîîÌÑ∞Ïóê ÏÇΩÏûÖ
        insertImageToEditor(result.data.localPath);
        setShowContextMenu(false);
      } else {
        throw new Error("Ïù¥ÎØ∏ÏßÄ URLÏùÑ Í∞ÄÏ†∏Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
      }
    } catch (err) {
      console.error("Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ± Ïò§Î•ò:", err);
      setError(err instanceof Error ? err.message : "Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsUploading(false);
    }
  };

  // ÏÉÅÏÑ∏ ÎÇ¥Ïö© ÏûëÏÑ± Ìï∏Îì§Îü¨
  const handleGenerateDetail = async () => {
    try {
      setError("");
      setIsUploading(true);

      const response = await fetch("http://localhost:3001/api/gpt/generate-detail", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          index: selectedLineText,
        }),
      });

      if (!response.ok) {
        throw new Error("ÏÉÅÏÑ∏ ÎÇ¥Ïö© ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }

      const result = await response.json();

      if (result.success && result.data) {
        // ÌòÑÏû¨ ÏÑ†ÌÉùÎêú ÎùºÏù∏ÏùÑ ÏÉÅÏÑ∏ ÎÇ¥Ïö©ÏúºÎ°ú ÎåÄÏ≤¥
        if (!textareaRef.current) return;

        const textarea = textareaRef.current;
        const textValue = textarea.value;

        // ÏÑ†ÌÉùÎêú ÎùºÏù∏Ïùò ÏãúÏûëÍ≥º ÎÅù ÏúÑÏπò Ï∞æÍ∏∞
        const selectionEnd = textarea.selectionEnd;

        // ÏÉÅÏÑ∏ ÎÇ¥Ïö©ÏúºÎ°ú ÎåÄÏ≤¥
        const beforeSelection = textValue.substring(0, selectionEnd);
        const afterSelection = textValue.substring(selectionEnd);
        const newContent = beforeSelection + `\n${result.data}\n` + afterSelection;

        setContent(newContent);
        setShowContextMenu(false);
      } else {
        throw new Error("ÏÉÅÏÑ∏ ÎÇ¥Ïö©ÏùÑ Í∞ÄÏ†∏Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
      }
    } catch (err) {
      console.error("ÏÉÅÏÑ∏ ÎÇ¥Ïö© ÏÉùÏÑ± Ïò§Î•ò:", err);
      setError(err instanceof Error ? err.message : "ÏÉÅÏÑ∏ ÎÇ¥Ïö© ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsUploading(false);
    }
  };

  // ÌîÑÎ¶¨Î∑∞Ïóê ÏóÜÎäî Ìé∏ÏßëÍ∏∞ ÌÖçÏä§Ìä∏ Ï†úÍ±∞
  function removeHyphens(str: string) {
    str = str.replace(/-/g, "");
    str = str.replace(/#/g, "");
    str = str.trim();
    return str;
  }

  // ÌÅ¥Î¶ΩÎ≥¥Îìú Ïù¥ÎØ∏ÏßÄ URL Ï≤òÎ¶¨ Ìï®Ïàò
  const handlePaste = async (e: React.ClipboardEvent<HTMLTextAreaElement>) => {
    const clipboardData = e.clipboardData;
    const items = clipboardData.items;

    // ÌÅ¥Î¶ΩÎ≥¥Îìú Îç∞Ïù¥ÌÑ∞ Ï§ë Ïù¥ÎØ∏ÏßÄ URL ÌôïÏù∏
    const text = clipboardData.getData("text");
    const isImageUrl =
      text &&
      (text.startsWith("http://") || text.startsWith("https://")) &&
      (text.endsWith(".png") || text.endsWith(".jpg") || text.endsWith(".jpeg") || text.endsWith(".gif"));

    // ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Ïù¥ÎØ∏ÏßÄ ÌååÏùºÏù¥ ÏûàÎäîÏßÄ ÌôïÏù∏
    for (let i = 0; i < items.length; i++) {
      if (items[i].type.indexOf("image") !== -1) {
        e.preventDefault(); // Í∏∞Î≥∏ Î∂ôÏó¨ÎÑ£Í∏∞ Î∞©ÏßÄ
        const file = items[i].getAsFile();
        if (file) {
          try {
            const imageUrl = await uploadImage(file);
            insertImageToEditor(imageUrl);
          } catch (err) {
            setError("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
          }
        }
        break;
      }
    }
  };

  // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ìï®Ïàò
  const uploadImage = async (file: File): Promise<string> => {
    setIsUploading(true);
    try {
      const formData = new FormData();
      formData.append("image", file);

      const response = await fetch("http://localhost:3001/api/upload-image", {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        throw new Error("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïã§Ìå®");
      }

      const result = await response.json();
      return result.imageUrl;
    } catch (err) {
      console.error("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïò§Î•ò:", err);
      throw err;
    } finally {
      setIsUploading(false);
    }
  };

  // ÌÖçÏä§Ìä∏Ïóê Ïù¥ÎØ∏ÏßÄ ÎßÅÌÅ¨ ÏÇΩÏûÖ
  const insertImageToEditor = (imageUrl: string) => {
    if (!textareaRef.current) return;

    const textarea = textareaRef.current;
    const endPos = textarea.selectionEnd;

    const beforeText = textarea.value.substring(0, endPos);
    const afterText = textarea.value.substring(endPos);
    const imageMarkdown = `![Ïù¥ÎØ∏ÏßÄ](${imageUrl})`;

    const newContent = beforeText + imageMarkdown + afterText;
    setContent(newContent);

    // Ïª§ÏÑú ÏúÑÏπò Ï°∞Ï†ï
    setTimeout(() => {
      textarea.focus();
      const newCursorPos = endPos + imageMarkdown.length;
      textarea.setSelectionRange(newCursorPos, newCursorPos);
    }, 0);
  };

  // ÌååÏùº ÏóÖÎ°úÎìú Ï≤òÎ¶¨
  const handleFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (!files || files.length === 0) return;

    try {
      const imageUrl = await uploadImage(files[0]);
      insertImageToEditor(imageUrl);
    } catch (err) {
      setError("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      // ÌååÏùº Ïù∏Ìíã Ï¥àÍ∏∞Ìôî
      e.target.value = "";
    }
  };

  // Ìèº Ï†úÏ∂ú Ï≤òÎ¶¨
  const handleSubmitForm = async () => {
    // Form validation
    if (!title.trim()) {
      setError("Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    if (!content.trim()) {
      setError("ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    try {
      setError("");
      await onSubmit({
        title,
        content,
        category,
        description,
        coverImage,
        tags,
      });
    } catch (err) {
      console.error("Ìèº Ï†úÏ∂ú Ïò§Î•ò:", err);
      setError(err instanceof Error ? err.message : "Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    }
  };

  const onClickCreateIndex = async () => {
    if (title.trim() === "") {
      setError("Î™©Ï∞® ÏÉùÏÑ±ÏùÑ ÏúÑÌï¥ Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    try {
      setIsGeneratingIndex(true);
      setError("");

      const response = await fetch("http://localhost:3001/api/gpt/generate-index", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          text: title,
        }),
      });

      if (!response.ok) {
        throw new Error("Î™©Ï∞® ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }

      const result = await response.json();

      if (result.success && result.data) {
        // ÌòÑÏû¨ ÏΩòÌÖêÏ∏† ÏïûÎ∂ÄÎ∂ÑÏóê Î™©Ï∞® Ï∂îÍ∞Ä
        const newContent = `## Î™©Ï∞®\n${result.data}\n\n${content}`;
        setContent(newContent);
      } else {
        throw new Error("Î™©Ï∞® Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
      }
    } catch (err) {
      console.error("Î™©Ï∞® ÏÉùÏÑ± Ïò§Î•ò:", err);
      setError(err instanceof Error ? err.message : "Î™©Ï∞® ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsGeneratingIndex(false);
    }
  };

  return (
    <div className={cx("container")}>
      <div className={cx("editor_header_bar")}>
        <div className={cx("editor_header_left")}>
          <Link href="/admin" className={cx("logo")}>
            SeedDividend
          </Link>
        </div>

        <div className={cx("editor_toolbar")}>
          <input
            type="file"
            id="image-upload"
            accept="image/*"
            style={{ display: "none" }}
            onChange={handleFileUpload}
          />
          <label htmlFor="image-upload" className={cx("toolbar_btn")}>
            <span className={cx("toolbar_icon", "image")}>üì∑ Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú</span>
          </label>
        </div>

        <div className={cx("editor_header_right")}>
          <button className={cx("header_action_btn")}>
            Í∏∞Î≥∏ Î™®Îìú
            <span className={cx("dropdown_arrow")}>‚ñæ</span>
          </button>
        </div>
      </div>

      <div className={cx("editor_container")}>
        <div className={cx("editor_header")}>
          <div className={cx("category_select_container")}>
            <select
              value={category}
              onChange={(e) => setCategory(e.target.value)}
              className={cx("category_select")}
              disabled={isSubmitting}
            >
              {categoryList.map((cat) => (
                <option key={cat} value={cat}>
                  {CATEGORY[cat]}
                </option>
              ))}
            </select>
            <div className={cx("select_arrow")}></div>
          </div>

          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            className={cx("title_input")}
            placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            disabled={isSubmitting}
          />
          <button
            type="button"
            onClick={onClickCreateIndex}
            disabled={isGeneratingIndex || isSubmitting || !title.trim()}
            className={cx("index_btn")}
          >
            {isGeneratingIndex ? "Î™©Ï∞® ÏÉùÏÑ± Ï§ë..." : "Î™©Ï∞® ÏÉùÏÑ±"}
          </button>
        </div>

        <div className={cx("editor_content_container")}>
          <div className={cx("editor_body")} ref={editorContainerRef}>
            <textarea
              ref={textareaRef}
              className={cx("content_editor", { uploading: isUploading })}
              value={content}
              onClick={handleCursorPosition}
              onChange={(e) => setContent(e.target.value)}
              placeholder="ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
              disabled={isSubmitting}
              onDoubleClick={handleDoubleClick}
              onPaste={handlePaste}
            />
            {isUploading && <div className={cx("upload_overlay")}>GPT ÏùëÎãµ ÎåÄÍ∏∞ Ï§ë...</div>}

            {showContextMenu && (
              <div
                ref={contextMenuRef}
                className={cx("context_menu")}
                style={{
                  position: "fixed",
                  left: `${menuPosition.x + 20}px`,
                  top: `${menuPosition.y}px`,
                  zIndex: 1000,
                }}
              >
                <button className={cx("context_menu_item")} onClick={handleGenerateDetail}>
                  ÏÉÅÏÑ∏ ÎÇ¥Ïö© ÏûëÏÑ±
                </button>
                <button className={cx("context_menu_item")} onClick={handleGenerateImage}>
                  Ïù¥ÎØ∏ÏßÄ ÏÉùÏÑ±
                </button>
              </div>
            )}
          </div>

          <div className={cx("preview_container")}>
            <div className={cx("preview_header")}>
              <h3>ÎØ∏Î¶¨Î≥¥Í∏∞</h3>
            </div>
            <div
              className={cx("post_content")}
              ref={previewContainerRef}
              dangerouslySetInnerHTML={{ __html: htmlContent }}
            ></div>
          </div>
        </div>

        <div className={cx("editor_footer")}>
          <div className={cx("editor_tools")}>
            <div className={cx("tool_buttons")}>
              <button className={cx("tool_btn")}>
                <span className={cx("tool_icon")}>üîç</span> ÎßûÏ∂§Î≤ï Í≤ÄÏÇ¨
              </button>
            </div>

            <div className={cx("action_buttons")}>
              <button className={cx("draft_btn")} disabled={isSubmitting}>
                ÏûÑÏãúÏ†ÄÏû•
              </button>
              <button
                className={cx("publish_btn")}
                onClick={handleSubmitForm}
                disabled={isSubmitting || isUploading}
                aria-busy={isSubmitting}
              >
                {isSubmitting ? "Ï†ÄÏû• Ï§ë..." : submitButtonText}
              </button>
            </div>
          </div>
        </div>

        {error && <div className={cx("error_toast")}>{error}</div>}
      </div>
    </div>
  );
}
