import rehypeExternalLinks from "rehype-external-links";
import remarkParse from "remark-parse";
import remarkRehype from "remark-rehype";
import rehypeHighlight from "rehype-highlight";
import remarkGfm from "remark-gfm";
import rehypeStringify from "rehype-stringify";
import rehypeRaw from "rehype-raw";
import { unified } from "unified";

export default async function markdownToHtml(markdown) {
  // Î®ºÏ†Ä ÎßàÌÅ¨Îã§Ïö¥ÏùÑ HTMLÎ°ú Î≥ÄÌôòÌï©ÎãàÎã§
  const result = await unified()
    .use(remarkParse)
    .use(remarkGfm)
    .use(remarkRehype, { allowDangerousHtml: true })
    .use(rehypeRaw)
    .use(rehypeExternalLinks, { target: ["_blank"] })
    .use(rehypeHighlight)
    .use(rehypeStringify)
    .process(markdown);

  // HTML Î¨∏ÏûêÏó¥Î°ú Î≥ÄÌôò
  let htmlContent = result.toString();

  // Í∞úÌñâ Î¨∏ÏûêÎ•º <br> ÌÉúÍ∑∏Î°ú ÎåÄÏ≤¥ (Ï†ïÍ∑úÏãù ÏÇ¨Ïö©)
  // Ïó∞ÏÜçÎêú Îëê Í∞ú Ïù¥ÏÉÅÏùò Í∞úÌñâÏùÄ <br><br>Î°ú Ï†úÌïú
  // htmlContent = htmlContent.replace(/\n{2,}/g, "<br><br>");
  // htmlContent = htmlContent.replace(/\n/g, "<br>");

  return htmlContent;
}
